import {
  BrowserRouter as Router,
  Routes,
  Route,
  Navigate,
  useLocation,
} from "react-router-dom";
import React, { useEffect } from "react";


import { useSelector, useDispatch } from "react-redux";
import { routesListSelector } from "modules/root/history/routeReducer";
import { setRoute } from "modules/root/history/routeActions";

// Component Imports
import App from "App";
import ToDoPage from "modules/feature/ToDoList/component/ToDoPage";
import Sandbox from "modules//feature/Sandbox/component/Sandbox";
export enum RoutesList {
  TO_DO_DASHBOARD = "/to-do/dashboard",
  DASHBOARD = "/dashboard",
  SANDBOX = "/sandbox",
  DEFAULT_DASHBOARD = "*",
}

const RouterMap = [
  { path: RoutesList.TO_DO_DASHBOARD, component: <ToDoPage /> },
  { path: RoutesList.DASHBOARD, component: <App /> },
  { path: RoutesList.SANDBOX, component: <Sandbox /> },
  { path: RoutesList.DEFAULT_DASHBOARD, component:<Navigate to={RoutesList.DASHBOARD} />},
];

const Routing = () => {
  const dispatch = useDispatch();
  const location = useLocation();
  const routeList = useSelector(routesListSelector);

  const knownLocation = Object.keys(RoutesList)
    .map((key) => RoutesList[key as keyof typeof RoutesList])
    .toString();
  useEffect(() => {
    const path = location.pathname;
    const finalPath = knownLocation.includes(path)
      ? path
      : RoutesList.DASHBOARD;

    if (
      routeList[routeList.length - 1] &&
      routeList[routeList.length - 1] === finalPath
    )
      return;
    else dispatch(setRoute(finalPath));
  }, [dispatch, knownLocation, location, routeList]);

  return (
    <Routes>
      {RouterMap.map((route) => {
        return <Route key={`route-${route.path}`} path={route.path} element={route.component} />
      })}
    </Routes>
  );
};

export default Routing;
