import { put, fork, takeLeading, select } from "redux-saga/effects";
import { ToDoListActions, updateList } from "modules/feature/ToDoList/actions/ToDoListActions";
import { getList } from "modules/feature/ToDoList/selector/ToDoListSelector";
import { IToDoListItem } from "modules/feature/ToDoList/type/ToDoListTypes";

function* setItemDone(action: any) {
    try{
        yield put(updateList([]));
    } catch(err) {
        console.error(`Error in setItemDone - `, err);
    }
}

function* addListItem(action: any) {
    try{
        const currList: IToDoListItem[] = yield select(getList);
        const {payload} = action;
        currList.push(payload);

        console.log(currList);
        yield put(updateList(currList));
    } catch(e){
        console.error("Error in AddListItem");
    }
}
function* watchToDoListSaga(){
    yield takeLeading(ToDoListActions.SET_ITEM_DONE, setItemDone);
    yield takeLeading(ToDoListActions.ADD_ITEM_TO_LIST, addListItem );
}

const toDoListSaga = fork(watchToDoListSaga);
export default toDoListSaga;