import { action } from "typesafe-actions";
import { IToDoListItem } from "modules/feature/ToDoList/type/ToDoListTypes";

export enum ToDoListActions {
    ADD_ITEM_TO_LIST = "ToDoListActions/ADD_ITEM_TO_LIST",
    SET_ITEM_DONE = "ToDoListActions/SET_ITEM_DONE",
    REMOVE_ITEM_FROM_LIST = "ToDoListActions/REMOVE_ITEM_FROM_LIST",
    UPDATE_LIST = "ToDoListActions/UPDATE_LIST"
};


export const addItem = (payload: IToDoListItem) => action(ToDoListActions.ADD_ITEM_TO_LIST, payload); 
export const setItemDone = (key: number) => action(ToDoListActions.SET_ITEM_DONE, key);
export const removeItem = (key: number) => action(ToDoListActions.REMOVE_ITEM_FROM_LIST, key);
export const updateList = (payload: IToDoListItem[]) => action (ToDoListActions.UPDATE_LIST, payload);