import { ToDoListActions } from "modules/feature/ToDoList/actions/ToDoListActions";
import { IToDoListReducerState } from "modules/feature/ToDoList/type/ToDoListTypes";

const initialState:IToDoListReducerState = {
    list: [],
}
const ToDoListReducer = (state = initialState, action:{ type: ToDoListActions, payload: any}) => {
    switch (action.type){
        case ToDoListActions.UPDATE_LIST:
            return {
                ...state,
                list: action.payload,
            };
        default: 
            return state;
    }
}
export default ToDoListReducer;